import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { FileService } from 'src/app/basic/service/core/file.service';
import { LanguagePackService } from 'src/app/basic/service/core/language-pack.service';
import { ApprovalAnswerType } from 'src/app/component/confirm/approval/approval.component';

@Component({
  selector: 'app-confirm-cc-detail-search',
  templateUrl: './confirm-cc-detail-search.page.html',
  styleUrls: ['./confirm-cc-detail-search.page.scss'],
})
export class ConfirmCcDetailSearchPage implements OnInit {

  @Input() permission = { master_company_all: false }
  @Input() form = {
    project_id: 0, //  ID
    master_company_id: 0, //  ID /  = 0
    company_id: 0, //  ID /  = 0
    start_date: null, //  
    end_date: null, //  
    approval_cnt_answer: null as ApprovalAnswerType, //  / , , , , 
    search_text: null, // 
    limit_no: 0 // 20 
  }

  temptForm = {
    project_id: 0, //  ID
    master_company_id: 0, //  ID /  = 0
    company_id: 0, //  ID /  = 0
    start_date: null, //  
    end_date: null, //  
    approval_cnt_answer: null as ApprovalAnswerType, //  / , , , , 
    search_text: null, // 
    limit_no: 0 // 20 
  }

  constructor(
    private _modal: ModalController,
    private file: FileService,
    public languagePack: LanguagePackService
  ) { }

  ngOnInit() {
    this.temptForm = this.file.clone(this.form);
  }

  reset() {
    this.temptForm = this.file.clone(this.form);
  }
  search() {
    const form = this.file.clone(this.temptForm);
    this._modal.dismiss(
      form
    );
  }

}
